<template>
  <div class="row">
    <div class="col s6 offset-s3">
      <h1>Create Todo</h1>

      <form>
        <div class="input-field">
          <input 
            id="title"
            type="text"
            placeholder="Title"
            class="validate"
            :class="{'invalid': $v.title.$error}"
            v-model="title"
            @input="$v.title.$touch()"
          >
          <!-- ??? срабатывают сразу, @blur не помогло ??? -->
          <span v-if="!$v.title.required" class="helper-text">Title is required</span>
          <span v-if="!$v.title.uniqTitle" class="helper-text">This title is already exist</span>
        </div>
        <ul class="browser-default">
          <li>
            <div class="input-field">
              <input
                id="item"
                type="text"
                title="push Enter to add"
                placeholder="Todo item"
                :class="{'invalid': itemError}"
                v-model="item"
                @keyup.enter="addItem"
                maxlength="50"
              >
              <span class="character-counter" style="float: right; font-size: 12px;">{{item.length}}/50</span>
              <span class="helper-text">{{itemError}}</span>
            </div>
          </li>
          <li v-for="(item, index) in itemsList" :key="item" style="position: relative">{{item}} <span class="remove" @click="removeItem(index)">x</span></li>
        </ul>
        <button class="btn" type="button" @click.prevent="addTodo" :disabled="itemsList.length === 0 || $v.title.$error">Create todo</button>
      </form>
    </div>
  </div>
</template>

<script>
import { required } from 'vuelidate/lib/validators'

export default {
  name: "create",
  data() {
    return {
      title: '',
      itemsList: [],
      item: '',
      itemError: '',
    }
  },
  methods: {
    addItem() {
      if (this.item.length === 0) {
        this.itemError = "Empty item";
      } else if (!!this.itemsList.find(item => item === this.item)) {
        this.itemError = "Item is already exist";
      } else  {
        this.itemsList.push(this.item);
        this.item = '';
      }
    },
    removeItem(index) {
      this.itemsList.splice(index, 1);
    },
    addTodo() {
      const todo = {
        title: this.title,
        itemsList: this.itemsList,
        id: Date.now()
      }
      this.$store.dispatch('createTodo', todo);
      this.title = '';
      this.item = '';
      this.itemsList = [];
    }
  },
  watch: {
    item() {
      this.itemError = '';
    }
  },
  created() {
    console.log('created', JSON.parse(localStorage.getItem('todos') || []))
  },
  validations: {
    title: {
      required,
      uniqTitle: function(newTitle) {
        if (newTitle === '') {
          return true
        } else {
          return new Promise((resolve, reject) => {
            setTimeout(() => {
              const arrTodos = JSON.parse(localStorage.getItem('todos') || []);
              const  value = arrTodos.find(item => item.title === newTitle);
              resolve(!value);
            }, 1000)
          })
        }
      },
    },
  }
};
</script>

<style lang="scss" scoped>
.remove {
  display: none;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  width: 14px;
  height: 14px;
  font-size: 12px;
  line-height: 10px;
  text-align: center;
  border-radius: 50%;
  background-color: #ccc;
  cursor: pointer;
}
li:hover > .remove {
  display: block;
}
.helper-text {
  color: #F44336;
}
</style>
